    padding: 1.5rem;
  }
  .demo-card .avatar {
    width: 64px;
    height: 64px;
    border-radius: 50%;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }
  .demo-card h3 {
    font-size: 1.25rem;
    font-weight: 600;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }
  .demo-card p {
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.875rem;
    line-height: 1.5;
  }
  .demo-card .badge {
    display: inline-block;
    padding: 0.25rem 0.75rem;
    background: var(--color-accent);
    color: white;
    font-size: 0.75rem;
    font-weight: 500;
    border-radius: 9999px;
    margin-top: 1rem;
  }
  .product-card {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 1rem;
    overflow: hidden;
  }
  .product-card .image {
    width: 100%;
    height: 200px;
    background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  }
  .product-card .content {
    padding: 1.25rem;
  }
  .product-card h4 {
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
  }
  .product-card .price {
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--color-accent);
  }
  .product-card .description {
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.875rem;
    margin: 0.5rem 0;
  }
  .product-card button {
    width: 100%;
    margin-top: 1rem;
    padding: 0.75rem;
    background: var(--color-accent);
    color: white;
    font-weight: 500;
    border-radius: 0.5rem;
    transition: background 0.2s;
  }
  .product-card button:hover {
    background: var(--color-primary);
  }
  .comment-card {
    display: flex;
    gap: 1rem;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 0.75rem;
  }
  .comment-card .avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
    flex-shrink: 0;
  }
  .comment-card .author {
    font-weight: 600;
    font-size: 0.875rem;
  }
  .comment-card .time {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.75rem;
  }
  .comment-card .text {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.875rem;
    margin-top: 0.25rem;
  }
